<template>
  <div class="sidebarct">
    <Menu @on-select="selectMenu" ref="Menu" theme="light" class="menuct" :active-name="active" :open-names="openname" width="auto">
      <Submenu v-for="item in routeMap" :key="item.name" :name="item.name">
        <template slot="title">
          <img class="iconImg" :src="item.iconImg" alt="">
          {{item.title}}
        </template>
        <MenuItem v-for="sub in item.children" :key="sub.name" :name="sub.name">{{sub.title}}</MenuItem>
      </Submenu>
    </Menu>
  </div>
</template>
<script>
  import vue from 'vue'
  import img1 from '@/assets/img/admin/1.png'
  import img2 from '@/assets/img/admin/2.png'
  import img3 from '@/assets/img/admin/3.png'
  import img4 from '@/assets/img/admin/4.png'
  import img5 from '@/assets/img/admin/5.png'
  import img6 from '@/assets/img/admin/6.png'
  import img7 from '@/assets/img/admin/7.png'

  const routeMap = [
    {
      name: '1',
      title: '首页',
      iconImg: img1,
      children: [
        {
          name: '1-1',
          title: '首页'
        },{
          name: '1-2',
          title: '意见反馈'
        },{
          name: '1-3',
          title: '公告'
        }
      ]
    },{
      name: '2',
      title: '我要下单',
      iconImg: img2,
      children: [
        {
          name: '2-1',
          title: '我要下单'
        },{
          name: '2-2',
          title: '我的购物车'
        }
      ]
    },{
      name: '3',
      title: '订单管理',
      iconImg: img3,
      children: [
        {
          name: '3-1',
          title: '我的订单'
        },{
          name: '3-2',
          title: '订单报表'
        },{
          name: '3-3',
          title: '出库查询'
        },{
          name: '3-4',
          title: '退货管理'
        }
      ]
    },{
      name: '4',
      title: '开票管理',
      iconImg: img4,
      children: [
        {
          name: '4-1',
          title: '开票申请'
        },{
          name: '4-2',
          title: '我的开票信息'
        }
      ]
    },{
      name: '5',
      title: '账单管理',
      iconImg: img5,
      children: [
        {
          name: '5-1',
          title: '在线回款'
        },{
          name: '5-2',
          title: '回款认领'
        },{
          name: '5-3',
          title: '往来明细账'
        }
      ]
    },{
      name: '6',
      title: '我的账户',
      iconImg: img6,
      children: [
        {
          name: '6-1',
          title: '账户信息'
        },{
          name: '6-2',
          title: '我的信用额度'
        },{
          name: '6-3',
          title: '三证上传'
        },{
          name: '6-4',
          title: '收货地址'
        },{
          name: '6-5',
          title: '修改密码'
        }
      ]
    },{
      name: '7',
      title: '证书管理',
      iconImg: img7,
      children: [
        {
          name: '7-1',
          title: '产品注册证'
        },{
          name: '7-2',
          title: '授权证书'
        },{
          name: '7-3',
          title: '法人委托书'
        },{
          name: '7-4',
          title: '销售承诺书'
        },{
          name: '7-5',
          title: '证书过期提醒'
        }
      ]
    }
  ]
  export default {
    name: 'SideBar',
    data() {
      return {
        openname: [],
        active: '',
        routeMap
      }
    },
    mounted() {
      this.init()
    },
    methods: {
      selectMenu(name) {
        this.$router.push({name})
      },
      init() {
        this.active = this.$route.name
        this.openname.splice(0,5,this.$route.meta.menuOpen)
        this.$refs.Menu.updateActiveName();
        this.$refs.Menu.updateOpened();
      }
    }
  }

</script>

<style lang="less" scoped>
  .sidebarct {
    background-color: #fff;
    height: 100%;
    .iconImg {
      position: relative;
      top: 2px;
      margin-right: 5px;
    }
  }
  .sidebaricon {
    font-size: 20px;
    position: relative;
    top: 2px;
  }
  .menuct {
    height: 100%;
  }
</style>



